/**************
EPISODE SELECTION
**************/

.select {
    overflow: hidden;
    background-color: grey;

}
.cards {

    width: 150%;
    height: 100%;
    color: white;
    text-align: center;
    text-shadow: 1px 1px .3px #1B1B1B;

    .card{
        position: relative;
        width: 14.28%;
        height: 100%;
        float: left;

        .content {
            width: 100%;
            position: absolute;
            top: 33%;
            left: 50%;
            transform: translate(-50%, -50%);


        }
    }
}
